import React from 'react'
import Img from 'gatsby-image'
import ReactMarkdown from "react-markdown"

const ImgAndDescription = (props) => {
    const imageData = props.imgData.childImageSharp.fluid
    const { description } = props
    return (
        <div className="pt-6 ">
            <div className="text-center py-8 ">
                <h1 className=" text-3xl leading-9 font-extrabold text-gray-900 sm:text-4xl sm:leading-10">Nutricionista y entrenador personal en Zaragoza</h1>
            </div>
            <div className="relative bg-white mb-16">

                <div className="  justify-center lg:absolute lg:left-0  lg:w-1/2">
                    <Img fluid={imageData} imgStyle={{ objectFit: 'contain' }} style={{ maxHeight: 'calc(80vh)' }} />

                </div>
                <div className="relative max-w-screen-xl mx-auto px-4 pb-8 sm:pb-12 sm:px-6 lg:pb-16">
                    <div className="  leading-7 text-gray-500 max-w-2xl mx-auto lg:max-w-none lg:mr-0 lg:ml-auto lg:w-1/2 lg:pl-10">
                        <ReactMarkdown source={description} />
                    </div>
                </div>
            </div>

        </div>
    )
}
export default ImgAndDescription