import React from 'react'
import heroIm from '../assets/img/heroIm.jpg'
import Img from 'gatsby-image'
import BackgroundImage from 'gatsby-background-image-es5'

const Hero = (props) => {
    const { slider1Title, slider1subTitle, slider1CTA, cover } = props
    const imageData = props.cover.childImageSharp.fluid
    return (
        <>
            <BackgroundImage
                Tag="section"
                className= "py-28 rounded"
                fluid={imageData}
                backgroundColor={`#040e18`}
            >
                <div className="pt-6 mx-auto max-w-screen-xl px-4 sm:pt-12 sm:px-6 ">
                    <div className="text-center">
                        <h2 className="text-white text-4xl tracking-tight leading-10 font-extrabold  sm:text-5xl sm:leading-none md:text-6xl ">
                            {slider1Title}
                        </h2>
                        <p className="mt-3 max-w-md mx-auto text-base text-white sm:text-lg md:mt-5 md:text-xl md:max-w-3xl">
                            {slider1subTitle}
                        </p>
                        <div className="mt-5 max-w-md mx-auto sm:flex sm:justify-center md:mt-8">
                            <div className="rounded-md shadow">
                                <a href="#" className="w-full hover:bg-blue-nano hover:text-white flex items-center justify-center px-8 py-3 border border-transparent text-base leading-6 font-medium rounded-md text-blue-nano bg-white  focus:outline-none focus:shadow-outline-indigo transition duration-150 ease-in-out md:py-4 md:text-lg md:px-10">
                                    {slider1CTA}
                                </a>
                            </div>

                        </div>
                    </div>
                </div>
                </BackgroundImage >
  </>
    )
}
export default Hero