import React from 'react'
import BackgroundImage from 'gatsby-background-image-es5'

const ServiciosNutricion = (props) => {
    const { title, mainService1, mainService2, services } = props
    const serviceArray = props.restoDeServicios.split(',')
    console.log('serviceArray is ', serviceArray)
    const servicioLeftPic = props.servicioLeftPic.childImageSharp.fluid
    const servicioRightPic = props.servicioRightPic.childImageSharp.fluid

    return (
        <div className="py-16">
            <div className="text-center w-full">
                <h2 class="pb-12 text-3xl leading-9 font-bold tracking-tight text-gray-800 sm:text-4xl sm:leading-10">
                    {title}
                </h2>
            </div>
            <div className="md:flex space-x-8">
                <div className="md:w-1/2">
                    <div className="mb-4 md:mb-0 rounded-md shadow-md">
                       < BackgroundImage
                            Tag="section"
                            className=" rounded-lg  cursor-pointer object-fill  "
                            style={{ height: '275px' }}
                            fluid={servicioLeftPic}
                            backgroundColor={`#040e18`}
                        >
                        </BackgroundImage>
                        <div className="text-center w-full pt-4">  <h3  className=" text-2xl leading-9 font-semibold tracking-tight text-black ">{mainService1}</h3></div>

                        {serviceArray && serviceArray.map((service, i) => {
                            if (i < 4) {
                                return (
                                    <button className=" w-full text-left py-2  px-4 my-2 rounded-md">
                                        <h4 className="text-gray-800 hover:text-blue-nano hover:font-bold text-lg ">{service}</h4>

                                    </button>
                                )
                            }
                        })}
                    </div>
                </div>
                <div className="md:w-1/2">
                <div className="rounded-md shadow-md">
                        <BackgroundImage
                            Tag="section"
                            className=" rounded-lg  cursor-pointer object-fill  "
                            style={{ height: '275px' }}
                            fluid={servicioRightPic}
                            backgroundColor={`#040e18`}
                        >
                        </BackgroundImage>
                        <div className="text-center w-full pt-4">  <h3  className=" text-2xl leading-9 font-semibold tracking-tight text-black ">{mainService2}</h3></div>

                        {serviceArray && serviceArray.map((service, i) => {
                            if (i >= 4) {
                                return (
                                    <button className=" w-full text-left py-2  px-4 my-2 rounded-md">
                                        <h4 className="text-gray-800 hover:text-blue-nano hover:font-bold text-lg ">{service}</h4>
                                    </button>
                                )
                            }
                        })}
                    </div>
                    
                </div>

            </div>
        </div>

    )
}
export default ServiciosNutricion