import React from "react"
import { StaticQuery, graphql } from "gatsby"

import Layout from "../components/layout"
import ArticlesComponent from "../components/articles"

import "../styles/main.css"
import Features from "../components/Features"
import ImgAndDescription from "../components/ImgAndDescription"
import HealthSport from "../components/HealthSport"
import Hero from "../components/Hero"
import CTA from "../components/CTA"
import { Testimonials } from "../components/Testimonials"
import ServiciosNutricion from "../components/ServiciosNutricion"
import NuevoYo from "../components/NuevoYo"
import NanoBg from "../components/NanoBg"
import { BlogLatest } from "../components/BlogLatest"
import FAQ from "../components/FAQ"
import PhotoSection from "../components/PhotoSection"

const IndexPage = () => (
  <Layout>
    <StaticQuery
      query={graphql`
      query {
        strapiHome(id: {eq: "Home_1"}) {
            slider3Title
            slider3Subtitle
            slider3CTA
            slider2Title
            slider2Subtitle
            slider2CTA
            slider1subTitle
            slider1CTA
            section2Title
            section2Description
            slider1Title
            puedesLeermeTitle
            puedesLeermeSubtitle
            puedesLeermeCTA
            pageTitle
            opinionesTitle
            objetivosTitle
            objetivosList
            objetivosDescription
            nuevoYoTitle
            nuevoYoDescription
            nuevoYo3Title
            nuevoYo3Subtitle
            nuevoYo3Description
            nuevoYo2Title
            nuevoYo2Subtitle
            nuevoYo2Description
            nuevoYo1Subtitle
            nuevoYo1Heading
            nuevoYo1Description
            miMisionTitle
            miMisionSubtitle
            miMisionCTA
            metodoSectionTitulo
            metodoSectionPart4Title
            metodoSectionPart4Description
            metodoSectionPart3Title
            metodoSectionPart2Title
            metodoSectionPart3Description
            metodoSectionPart2Description
            metodoSectionPart1Title
            metodoSectionPart1Description
            metodoSectionDescription
            latestBlogTitle
            igTitle
            igDescrition
            igCTA
            id
            healthSportTitle
            healthSportDescription
            faqTitle
            faq5Title
            faq5Description
            faq4Title
            faq4Description
            faq3Title
            faq3Description
            faq2Title
            faq2Description
            faq1Title
            faq1Description
            beneficiosTitle
            beneficio5Title
            beneficio5Description
            beneficio4Title
            beneficio4Description
            beneficio3Title
            beneficio3Description
            beneficio2Title
            beneficio2Description
            beneficio1Title
            beneficio1Description
            NuevoYoCTA
            servicesTitle
            service1
            mainService2
            
            restoDeServicios
            servicioLeftPic {
              childImageSharp {
                fluid 
                {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            picQuoteSection {
              childImageSharp {
                fluid (duotone: { highlight: "#808080", shadow: "#192550", opacity: 50 })
                {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            servicioRightPic {
              childImageSharp  {
                fluid
                {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            entrenadorPersonalPic {
              childImageSharp {
                fluid
                {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            nanoAlutizHealthSport {
              childImageSharp {
                fluid (duotone: { highlight: "#0ec4f1", shadow: "#192550" })
                {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      `}
      render={data => (
        <div className="">
          <Hero slider1Title={data.strapiHome.slider1Title}
            slider1CTA={data.strapiHome.slider1CTA}
            slider1subTitle={data.strapiHome.slider1subTitle}
            cover={data.strapiHome.nanoAlutizHealthSport} />
          <div className="p-1 sm:container ">
            <ImgAndDescription description={data.strapiHome.section2Description} imgData={data.strapiHome.entrenadorPersonalPic} />
          </div>
          <CTA title={data.strapiHome.puedesLeermeTitle} subtitle={data.strapiHome.puedesLeermeSubtitle} CTA={data.strapiHome.puedesLeermeCTA} />
          <div className="container ">
            <ServiciosNutricion title={data.strapiHome.servicesTitle} mainService1={data.strapiHome.service1}
              mainService2={data.strapiHome.mainService2}
              servicioLeftPic={data.strapiHome.servicioLeftPic}
              servicioRightPic={data.strapiHome.servicioRightPic}
              restoDeServicios={data.strapiHome.restoDeServicios}
            />
          </div>

          <CTA title={data.strapiHome.miMisionTitle} subtitle={data.strapiHome.miMisionSubtitle} CTA={data.strapiHome.miMisionCTA} />
          <div className="container">
            <NuevoYo title={data.strapiHome.nuevoYoTitle} description={data.strapiHome.nuevoYoDescription}
              heading1={data.strapiHome.nuevoYo1Heading} description1={data.strapiHome.nuevoYo1Description}
              subtitle1={data.strapiHome.nuevoYo1Subtitle}
              heading2={data.strapiHome.nuevoYo2Heading} description2={data.strapiHome.nuevoYo2Description}
              subtitle2={data.strapiHome.nuevoYo2Subtitle}
              heading3={data.strapiHome.nuevoYo3Heading} description3={data.strapiHome.nuevoYo3Description}
              subtitle3={data.strapiHome.nuevoYo3Subtitle}
              CTA={data.strapiHome.nuevoYoCTA}
            />

          </div>
        <NanoBg bg={data.strapiHome.nanoAlutizHealthSport}/>
          <Features title={data.strapiHome.metodoSectionTitulo} description={data.strapiHome.metodoSectionDescription}
          part1Title={data.strapiHome.metodoSectionPart1Title}  part1Description={data.strapiHome.metodoSectionPart1Description} 
          part2Title={data.strapiHome.metodoSectionPart2Title}  part2Description={data.strapiHome.metodoSectionPart2Description} 
          part3Title={data.strapiHome.metodoSectionPart3Title}  part3Description={data.strapiHome.metodoSectionPart3Description} 
          part4Title={data.strapiHome.metodoSectionPart4Title}  part4Description={data.strapiHome.metodoSectionPart4Description} 
          />

          <Testimonials />
          <div className="container text-center py-16">
            <h2 className="text-3xl leading-9 font-extrabold text-gray-900 sm:text-4xl sm:leading-10">Objetivos nutricionales y deportivos</h2>
            <p className="text-gray-900 text-xl py-4">
              I'm baby deep v marfa kale chips offal fam chillwave mixtape tilde seitan ethical austin succulents. Everyday carry readymade cred kale chips normcore mustache. Deep v williamsburg edison bulb cray wayfarers. Church-key master cleanse humblebrag thundercats, tumeric lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>
            <div className="flex justify-between">
              <div className="w-1/2">
                <p className="text-gray-900 text-base py-4">
                  lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>
                <p className="text-gray-900 text-base py-4">
                  lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>
                <p className="text-gray-900 text-base py-4">
                  lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>
                <p className="text-gray-900 text-base py-4">
                  lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>

              </div>
              <div className="w-1/2">

                <p className="text-gray-900 text-base py-4">
                  lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>
                <p className="text-gray-900 text-base py-4">
                  lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>
                <p className="text-gray-900 text-base py-4">
                  lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>
                <p className="text-gray-900 text-base py-4">
                  lyft etsy wolf succulents DIY. Lo-fi distillery locavore glossier occupy.
                   </p>

              </div>
            </div>
          </div>
          <CTA title={data.strapiHome.igTitle} subtitle={data.strapiHome.igDescrition} CTA={data.strapiHome.igCTA} />
          <div className="container">
            {/* <HealthSport title={data.strapiHome.healthSportTitle} description={data.strapiHome.healthSportDescription} imgData={data.strapiHome.healthSportPicture} />  */}
          </div>
          <PhotoSection bg={data.strapiHome.picQuoteSection} title={data.strapiHome.photoSectionDescription}/>
          <BlogLatest/>
          <FAQ/>
          {/* <ArticlesComponent articles={data.allStrapiArticle.edges} /> */}
        </div>
      )}
    />
  </Layout>
)

export default IndexPage
