import React from 'react'
import BackgroundImage from 'gatsby-background-image-es5'
import '../styles/background-image.css'
const PhotoSection = (props) => {
    const bg=props.bg.childImageSharp.fluid
    const {title} = props
    return (
            <BackgroundImage
                Tag="section"
                className="black-overlay"
                style={{ height: '350px', opacity:'1 !important'}}
                fluid={bg}
                backgroundColor={`#000`}
            >
                <div className="content-box">
                {/* <p className="pt:12 md:pt-32 max-w-md mx-auto font-semibold text-white text-lg md:text-3xl mt-5 md:text-xl ">
                            {title}
                        </p> */}
                <p className="pt:12 max-w-xl mx-auto font-extrabold text-white text-lg md:text-2xl mt-5  ">
                ¿Buscas el antídoto para todos los mitos sobre alimentación y nutrición? ¿Necesitas respuestas reales? Cambiemos por dentro para que se note por fuera. 
                        </p>

                </div>
            </BackgroundImage>
    )
}
export default PhotoSection